<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <polygon :points="computedPoints"
                    :stroke="circleConfig.stroke"
                    :stroke-width="circleConfig.strokeWidth"
                    :fill="circleConfig.fill"
                    :fill-rule = "circleConfig.fillRule"></polygon>
          </svg>
        </div>

        <div class="svg-config-form__wrapper">
          <el-form ref="circleConfigForm" :model="circleConfig" label-width="200px">
            <el-form-item label="随机点">
                <el-checkbox v-model="showRandomPoint"/>
            </el-form-item>
            <el-form-item label="点">
              <el-checkbox-group v-model="checkedPoints">
                <el-checkbox v-for="point in points" :key="point" :label="point"/>
              </el-checkbox-group>
            </el-form-item>

            <el-form-item label="笔划颜色(stroke)">
              <el-color-picker v-model="circleConfig.stroke" show-alpha >
              </el-color-picker>
              {{circleConfig.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth)">
              <el-slider
                  v-model="circleConfig.strokeWidth"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="填充颜色(fill)">
              <el-color-picker v-model="circleConfig.fill" show-alpha >
              </el-color-picker>
              {{circleConfig.fill}}
            </el-form-item>

            <el-form-item label="填充规则(fillRule)">
              <el-select v-model="circleConfig.fillRule" clearable placeholder="请选择">
                <el-option label="nonzero" value="nonzero"/>
                <el-option label="evenodd" value="evenodd"/>
                <el-option label="inherit" value=""/>
              </el-select>
            </el-form-item>



          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  name: 'SVGDemo001',
  data () {
    return {
      intervals: [],
      timestamp: null,
      showRandomPoint: false,
      points: ['100,10', '40,180', '190,60', '10,60', '160,180'],
      checkedPoints: ['100,10', '40,180', '190,60', '10,60', '160,180'],
      circleConfig: {
        stroke: 'rgba(0, 0, 0, 0.8)',
        strokeWidth: 2,
        fill: 'rgba(255, 69, 0, 0.68)',
        fillRule: 'nozero'
      }
    }
  },
  created () {
    let intervalEvent = setInterval(() => {
      this.timestamp = new Date()
    }, 500)
    this.intervals.push(intervalEvent)
  },
  beforeDestroy () {
    for (let handler in this.intervals) {
      clearInterval(handler)
    }
  },
  methods: {
    getRandomInt (min, max) {
      min = Math.ceil(min)
      max = Math.floor(max)
      return Math.floor(Math.random() * (max - min)) + min // The maximum is exclusive and the minimum is inclusive
    }
  },
  computed: {
    computedPoints () {
      if (this.showRandomPoint && this.timestamp) {
        let cnt = this.getRandomInt(3, 10)
        let points = []
        for (let i = 0; i < cnt; i++) {
          const x = this.getRandomInt(10, 290)
          const y = this.getRandomInt(10, 290)
          points.push(`${x},${y}`)
        }
        return points.join(' ')
      }
      return this.checkedPoints.join(' ')
    }
  }
}
</script>

